.newsletter{
  height: 40vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: auto;
  padding: 0px 140px;
  padding-bottom: 150px;
  background: linear-gradient(90deg, #fff, #686868);
  gap: 30px;
}
.newsletter h1{
  color: #010000;
  font-size: 55px;
  font-weight: 600;
  text-shadow: 3px 3px #fff;
}
.newsletter p{
  color: #000000;
  font-size: 20px;
  text-shadow: 2px 2px #fff;
}
.newsletter div{
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: white;
  width: 730px;
  height: 70px;
  border-radius: 80px;
  border: 1px solid #e3e3e3;
}
.newsletter input{
  width: 500px;
  margin-left: 30px;
  border: none;
  color: #616161;
  font-family: Poppins;
  font-size: 16px;
}
.newsletter div button{
  width: 210px;
  height: 70px;
  border-radius: 80px;
  border: none;
  color: white;
  font-size: 16px;
  background-color: #007bff;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.3s ease;
}
.newsletter div button:hover{
  background-color: #0056b3;
  transform: scale(1.03);
}

@media(max-width:1280px){
  .newsletter{
    padding: 0px 80px;
    padding-bottom: 120px;
    gap: 20px;
  }
  .newsletter h1{
    font-size: 36px;
  }
  .newsletter p{
    font-size: 16px;
  }
  .newsletter div{
    width: 600px;
    height: 60px;
  }
  .newsletter div input{
    width: 400px;
  }
  .newsletter div button{
    width: 160px;
    height: 60px;
  }
}
@media(max-width:1024px){
  .newsletter{
    padding-bottom: 60px;
  }
  .newsletter h1{
    font-size: 30px;
  }
  .newsletter p{
    font-size: 14px;
  }
  .newsletter div{
    width: 500px;
    height: 50px;
  }
  .newsletter div input{
    width: 300px;
    font-size: 14px;
  }
  .newsletter div button{
    width: 120px;
    height: 50px;
    font-size: 14px;
  }
}
@media(max-width:800px){
  .newsletter{
    padding-bottom: 30px;
    height: 35vh;
  }
  .newsletter h1{
    font-size: 18px;
  }
  .newsletter p{
    font-size: 12px;
  }
  .newsletter div{
    width: 400px;
    height: 40px;
  }
  .newsletter div input{
    width: 200px;
    font-size: 13px;
  }
  .newsletter div button{
    width: 100px;
    height: 40px;
    font-size: 13px;
  }
}
@media(max-width:500px){
  .newsletter{
    height: 25vh;
    width: 100%;
    padding: 0;
    gap: 15px;
  }
  .newsletter h1{
    font-size: 16px;
    text-shadow: 1px 1px #fff;
  }
  .newsletter p{
    font-size: 13px;
    max-width: 200px;
    text-align: center;
    text-shadow: 1px 1px #fff;
  }
  .newsletter div{
    width: 290px;
  }
  .newsletter div input{
    width: 130px;
  }
}