.offers{

  height: 100vh;
  display: flex;
  margin: auto;
  margin-top: -20vh;
  padding: 0px 140px;  
  text-shadow: 3px 3px white;
  background: linear-gradient(90deg, #fff, #686868);
}
.offers-left{
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 10vw;
}
.offers-left h1{
  color: #171717;
  font-size: 80px;
  font-weight: 600;
  
}
.offers-left p{
  color: #171717;
  font-size: 22px;
  font-weight: 600;
}
.offers-left button{
  width: 282px;
  height: 70px;
  border-radius: 35px;
  background-color: #007bff;
  border: none;
  color: white;
  font-size: 22px;
  font-weight: 500;
  margin-top: 30px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.3s ease;
}
.offers-right{
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-top: 50px;
  margin-right: 10vw;
}
.offers-left button:hover{
  background-color: #0056b3;
  transform: scale(1.03);
}

@media(max-width:1280px){
  .offers{
    padding: 0px 80px;
    padding-bottom: 120px;
  }
  .offers-left h1{
    font-size: 40px;
  }
  .offers-left p{
    font-size: 18px;
  }
  .offers-left button{
    width: 220px;
    height: 50px;
    font-size: 18px;
  }
  .offers-right img{
    width: 300px;
  }
  .offers-right{
    padding-top: 30px;
  }
}
@media(max-width:1024px){
  .offers{
    padding: 0px 60px;
    padding-bottom: 80px;
    height: 40vh;
  }
  .offers-left h1{
    font-size: 30px;
  }
  .offers-left p{
    font-size: 16px;
  }
  .offers-left button{
    width: 160px;
    height: 40px;
    font-size: 16px;
  }
  .offers-right img{
    width: 200px;
  }
  .offers-right{
    padding-top: 10px;
  }
}
@media(max-width:800px){
  .offers{
    padding: 60px;
  }
  .offers-left h1{
    font-size: 22px;
    text-shadow: 1px 1px #fff;
  }
  .offers-left p{
    font-size: 13px;
    text-shadow: 1px 1px #fff;
  }
  .offers-left button{
    width: 140px;
    height: 30px;
    font-size: 12px;
  }
  .offers-right img{
    width: 180px;
  }
}
@media(max-width:500px){
  .offers{
    height: 25vh;
    padding-bottom: 50px;
  }
  .offers-left h1{
    font-size: 16px;
  }
  .offers-left p{
    font-size: 10px;
  }
  .offers-left button{
    width: 120px;
    height: 25px;
    font-size: 10px;
    margin-top: 12px;
  }
  .offers-right img{
    width: 120px;
  }
}