.new-collections{
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  padding-bottom: 100px;
  background: linear-gradient(90deg, #fff, #686868);
}
.new-collections h1{
  color: #171717;
  font-size: 50px;
  font-weight: 600;
  text-shadow: 3px 3px white;
}
.new-collections hr{
  width: 200px;
  height: 6px;
  box-shadow: 3px 3px white;
  border-radius: 10px;
  background: #252525;
}
.collections{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  margin-top: 50px;
  gap: 30px;
}

@media(max-width:1280px){
  .new-collections h1{
    font-size: 40px;
  }
  .new-collections hr{
    width: 160px;
    height: 4px;
  }
  .collections{
    gap: 20px;
    margin-top: 30px;
  }
}
@media(max-width:1024px){
  .new-collections h1{
    font-size: 30px;
  }
  .new-collections hr{
    width: 120px;
    height: 3px;
  }
  .collections{
    gap: 15px;
    margin-top: 20px;
  }
}
@media(max-width:800px){
  .new-collections{
    gap: 6px;
  }
  .new-collections h1{
    font-size: 20px;
    text-shadow: 1px 1px #fff;
  }
  .new-collections hr{
    width: 100px;
    box-shadow: 1px 1px #fff;
  }
  .collections{
    gap: 5px;
  }
}
@media(max-width:500px){
  .collections{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
  }
}